import {useSelect} from '@wordpress/data';
import {useEffect} from '@wordpress/element';
import {registerPlugin} from '@wordpress/plugins';

const render = () => {
    const cartData = useSelect((select) => select('wc/store/cart').getCartData());
    const freeShippingBannerData = cartData.extensions?.yith_sales_wc_block_manager?.yith_sales_remain_for_free_shipping;
    useEffect(() => {
        if (typeof freeShippingBannerData !== 'undefined') {
            const boydClass = document.body.classList;
            if (Array.from(boydClass).includes('woocommerce-cart')) {
                const hide = freeShippingBannerData.length === 0;
                const banners = document.querySelectorAll('.yith-sales-free-shipping-banner');
                if (banners.length) {
                    banners.forEach(banner => {
                        const id = banner.id;
                        const campaignID = id.split('--')[1];
                        if (hide) {
                            banner.classList.add('yith_sales_banner_hide');
                        } else {
                            banner.classList.remove('yith_sales_banner_hide');
                            const newText = freeShippingBannerData.filter(newData => parseInt(newData.id) === parseInt(campaignID)).shift();
                            if (newText) {
                                banner.querySelector('.yith-sales-free-shipping-text').innerHTML = newText.text;
                            }
                        }
                    });
                } else {
                    if (!hide) {
                        window.location.reload();
                    }
                }
            }
        }
    }, [freeShippingBannerData]);
}

registerPlugin('yith-sales-free-shipping-banner', {
    render,
    scope: 'woocommerce-checkout',
});
